.rsc-footer {
  width: 100vw;
  background: var(--rsc-footer-bg-color);
  position: relative;

  .rsc-footer-form {
    width: 100vw;
    margin-bottom: -146px;
    margin-left: -24px;
    height: 640px;
    border-radius: 0% 0% 0% 70px;
    transform: rotate(175deg);
    background-color: #e5e5e5;
    background-size: 400% 400%;
    animation: gradient 6s ease-in-out infinite;
  }
  .rsc-footer-container {
    position: absolute;
    top: 24px;
    width: 80vw;
    height: 452px;
    padding: 12px;
    // border: 1px solid black;

    .menu-container {
      .rsc-title {
        margin-bottom: 8px;
      }
      .sitemap-entries {
        margin-bottom: 40px;

        .entry {
          margin-bottom: 8px;
          a {
            text-decoration: none;
            color: var(--dark-bg);
          }
        }
      }
    }

    .social-container {
      .rsc-title {
        margin-bottom: 8px;
      }

      a {
        font-size: 28px;
        color: #9e9c9c;
      }
      a:not(:last-child) {
        margin-right: 8px;
      }
    }

    .copyright-container {
      margin-top: 40px;
      color: #9e9c9c;

      a {
        color: #9e9c9c;
      }
    }
  }

  .container {
    position: absolute;
    top: 200px;
    left: 120px;
    width: 90%;
    height: 320px;
    // background-color: yellow;
    transform: rotate(-175deg);
    padding: 24px 32px;
    box-sizing: border-box;

    .content {
      display: flex;
      border-bottom: 1px solid #b4b2b2;
      padding-bottom: 12px;
      margin-bottom: 24px;
    }

    .menu-container {
      box-sizing: border-box;
      min-width: 220px;
      margin-left: 220px;
      padding: 12px 8px;

      .title {
        h2 {
          //TODO: Refactor
          margin: 0;
        }
      }
      .entries {
        .entry {
          a {
            text-decoration: none;
            color: var(--bg-color);
          }

          &:first-child {
            margin-top: 32px;
          }
          &:not(:last-child) {
            margin-bottom: 32px;
          }
        }
      }
    }
    .social-container {
      box-sizing: border-box;
      min-width: 220px;
      margin-left: 220px;
      padding: 12px 8px;

      .title {
        h2 {
          //TODO: Refactor
          margin: 0;
        }
      }
      .socials {
        .row {
          margin-top: 8px;
          a {
            font-size: 32px;
            padding: 4px;
            color: #b4b2b2;

            &:not(:last-child) {
              margin-right: 24px;
            }
          }
        }
      }
    }

    .bottom-container {
      color: #b4b2b2;

      .creator {
        a {
          color: #b4b2b2;
        }
      }
    }
  }
}
